/**
 * 用途: 重置css默认样式
 * 作者: kezh
 * 日期: 2017/01/13
 * 说明: 本reset文件只重置项目中使用到的标签的样式
 * 由于本项目引用了bootstrap.css,因此和bootstrap.css中重复的部分将省略
 * 同时bootstrap.css中需要重置的公共样式也在此设置
 */
html {
  font-family: "微软雅黑", Tahoma, Arial, "Hiragino Sans GB", sans-serif;
  line-height: 1.5;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
html,
body,
h1,
h2,
h3,
h4,
p,
form,
fieldset,
button,
input,
select,
ul,
ol,
dl,
dd,
hr {
  padding: 0;
  margin: 0;
}
h1,
h2,
h3,
h4,
i,
em,
b,
strong,
th,
dt {
  font-weight: 100;
}
i,
em {
  font-style: normal;
}
button,
input {
  font-family: "微软雅黑", Tahoma, Arial, "Hiragino Sans GB", sans-serif;
}
a {
  text-decoration: none;
}
a:active,
a:hover,
a:link,
a:visited {
  text-decoration: none;
}
ul,
ol {
  list-style: none;
}
button,
input {
  background-color: transparent;
  outline: none;
  border: none;
}
input:focus {
  border: 1px solid #71aff6;
}
select {
  border: none;
  outline: none;
}
select:focus,
select:active {
  outline: none;
}
textarea {
  border: none;
  outline: none;
  resize: none;
  overflow-x: hidden;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
canvas {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.bg-green {
  background-color: #50c14e;
}
.bg-green:hover {
  background-color: #56d954;
}
.bg-green:active {
  background-color: #3eb53c;
}
.bg-blue {
  background-color: #5093e1;
}
.bg-blue:hover {
  background-color: #71aff6;
}
.bg-blue:active {
  background-color: #3978c2;
}
.bg-red {
  background-color: #f65177;
}
.bg-red:hover {
  background-color: #ff688b;
}
.bg-red:active {
  background-color: #d73a5e;
}
.bg-yellow {
  background-color: #ffcf76;
}
.bg-yellow:hover {
  background-color: #ffdd9d;
}
.bg-yellow:active {
  background-color: #e2ae4f;
}
.bg-purple {
  background-color: #492a8b;
}
.bg-purple:hover {
  background-color: #6f46C7;
}
.bg-purple:active {
  background-color: #33176d;
}
.bg-black {
  background-color: #2d313a;
}
.bg-black:hover {
  background-color: #515766;
}
.bg-black:active {
  background-color: #08090c;
}
.bg-gray {
